@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;600&display=swap);
:root {
  --primary-dark4: #000000;
  --primary-dark3: #e45c3a; /* Orange Soda */
  --primary-dark2: #d7431d; /* Vermillion */
  --primary-dark1: #b43718; /* Chinese Red */
  --primary: #e76f51; /* Burnt Sienna */
  --primary-light2: #eb886f; /* Middle Red */
  --primary-light3: #eb886f; /* Vivid Tangerine */
  --primary-light1: #eb886f; /* Melon */
  --light: #f8d2c9; /* Pale Pink*/

  --charcoal: #264653;
  --persian-green: #2a9d8f;
  --prussian-blue: #00547A;

  --secondary: #E9C46A;
  --secondary-dark: #E2B33C;


  --max-width: 1440px;

  --font1: "Quicksand", sans-serif;
  --button-size: 3rem;
}

.title {
  display: flex;
  justify-self: center;
  justify-content: center;
  font-size: 50px;
  margin: 2rem;
  color: #f8d2c9;
  color: var(--light);
}

@media screen and (max-width: 500px) {
  .title {
    flex-flow: column;
    align-items: center;
  }
}

.title::after {
  content: "‚úåüèº";
}

body {
  margin: 0;
  /* background: var(--persian-green); */
  background: #00547A;
  background: var(--prussian-blue);
  font-family: "Quicksand", sans-serif;
  font-family: var(--font1);
  display: grid;
  grid-template-columns: 1fr minmax(auto, 1440px) 1fr;
  grid-template-columns: 1fr minmax(auto, var(--max-width)) 1fr;
}

#root {
  grid-column: 2/-2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button {
    background: var(--primary-light1);
    border: 1px solid var(--primary-dark4);
    font-size: 24px;
}

.button:hover {
    background: var(--primary-dark2);
    color: var(--light);
}
.integerKeypad {
    display: grid;
    grid-template-columns: repeat(3, var(--button-size));
    grid-template-rows:repeat(4, var(--button-size));
    grid-column: 1/4;
    grid-row: 2/5;
}

.integerKeypad .button:nth-child(10) {
    grid-column: 1/3;
}
.optionsKeypad {
  display: grid;
  grid-template-columns: repeat(5, var(--button-size));
  grid-template-rows: repeat(1, var(--button-size));
  grid-column: 1/5;
  grid-row: 1/2;
}

.optionsKeypad .button {
    background: var(--secondary);
}
.optionsKeypad .button:hover {
    background: var(--secondary-dark);
    color: var(--primary-dark4)
}

.optionsKeypad button:first-child,
.optionsKeypad button:last-child {
  font-size: 20px;
}

.optionsKeypad button:last-child {
    grid-column: 4/6;
  }

.operationKeypad {
    display: grid;
    grid-template-columns: repeat(2, var(--button-size));
    grid-template-rows: repeat(5, var(--button-size));
    grid-column: 4/6;
    grid-row: 2/6;
}

.operationKeypad .button:last-child {
    grid-column: 1/3;
}

.calculator-display {
  font-size: 20px;
  background: white;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  min-height: 48px;
  border: 1px solid black;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;

}

.calculator-display p {
    margin: 0;
    padding: 10px;
    word-break: break-all;
}

.calculator__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    margin:3rem auto;
    width: 15rem;
}

.integer-operation__container {
    display: grid;
    grid-template-columns: repeat(5, var(--button-size));
    grid-template-rows: repeat(5, var(--button-size));
}


.error {
    margin-top: 2rem;
    width: 10rem;
    text-align: center;
}

.error h4 {
    margin: 0;
    font-size: 24px;
    line-height: 32px;
    background-color: #AD343E;
    border-radius: 12px;
    padding: 10px;
}

.error p {
    color: var(--light);
    font-size: 18px;
    
}
